.hero {
  background-color: #007bff; 
  color: #fff; 
  padding: 100px 0; 

  text-align: left; 
}

.hero__content {
  max-width: 1200px; 
  margin: 0 auto; 

 
}


.hero__header {
  font-size: 36px; 
  font-weight: bold;
  margin-bottom: 20px; 
}

.hero__subheader {
  font-size: 24px;
}

.btn-primary-hero {
  display: inline-block;
  padding: 15px 30px;
  font-size: 18px;
  font-weight: bold;
  text-transform: uppercase;
  text-decoration: none;
  background-color: black; 
  color: #fff;
  border: 2px solid black; 
  border-radius: 5px;

  
}



@media screen and (max-width: 321px) {


.hero__content {
   display:flex;
  flex-direction: column;
  align-items: center;
}
  .hero__header {
    font-size: 24px;
    text-align: center; 
    margin: 0; 
  }

  .hero__subheader {
    font-size: 12px;
    text-align: center; 
    margin-top: 10px; 
  }

  .btn-primary-hero {
    font-size: 14px;
    padding: 10px 20px;
  }
}


@media screen and (max-width: 426px) {

  .hero__content {
   display:flex;
  flex-direction: column;
  align-items: center;
}

    .hero__header {
    font-size: 24px;
        text-align: center; 
    margin: 0; 

  }

  .hero__subheader {
    font-size: 16px;
     text-align: center; 
    margin-top: 10px; 

  }

  .btn-primary-hero {
    font-size: 16px;
    padding: 10px 20px;
  
  }

}


@media screen and (max-width: 769px) {

  .hero__content {
   display:flex;
  flex-direction: column;
  align-items: center;
}

    .hero__header {
    font-size: 28px;
      text-align: center; 
    margin: 0; 

  }

  .hero__subheader {
    font-size: 18px;
     text-align: center; 
    margin-top: 10px; 

  }

  .btn-primary-hero {
    font-size: 18px;
    padding: 10px 20px;
  
  }

}

@media screen and (max-width: 1025px) { 

    .hero__content {
      padding-left: 25px;

}

}
