.signup-section {
  background-color: #000; 
  color: #fff; 
  padding: 80px 0; 
  text-align: center; 
}

.signup-form {
  max-width: 600px; 
  margin: 0 auto; 
  padding: 20px; 
}

.signup-form h2 {
  font-size: 28px;
  margin-bottom: 30px; 
}

.form-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr); 
  grid-gap: 20px; 
  grid-template-areas:
    "firstName lastName"
    "workEmail workPhone";

}

.form-grid-input {
  width: 95%;
  padding: 10px;
  font-size: 16px;
  border: none;
  border-radius: 5px;
  background-color: #333;
  color: #fff;
}



.form-group {
  margin-bottom: 20px; 

  display: flex;
  flex-direction: column;
  align-items: flex-start;

}

.form-group label {
  display: block; 
  font-size: 18px; 
  margin-bottom: 5px; 
}


#companyName {
    width: 98%; 
  padding: 10px; 
  font-size: 16px; 
  border: none; 
  border-radius: 5px; 
  background-color: #333; 
  color: #fff;


}


.btn-primary-form {
  display: inline-block;
  padding: 15px 30px;
  font-size: 18px; 
  font-weight: bold;
  text-transform: uppercase; 
  text-decoration: none;
  background-color: #007bff; 
  color: #fff; 
  border: none; 
  border-radius: 5px; 
  transition: background-color 0.2s, color 0.2s;
}

.btn-primary:hover {
  background-color: #0056b3; 
}